  import ua from "./utils/userAgent";
export default {
  name: 'App',
  data () {
    return {
      uaSort: {
        webkitOverflowScrolling: '',
      },}
    },
  created(){
    if(ua.versions.android){
      this.uaSort.webkitOverflowScrolling = 'touch';
    }
  }
}



